<template>
  <div class="banner slider-container">
    <div class="help">
      <p><img src="../../static/img/help.png"></p>
      <ul>
        <li>
          <a href="#">如何获取星云币？</a>
        </li>
        <li>
          <a href="https://github.com/ChengOrangeJu/WebExtensionWallet">如何使用钱包插件？</a>
        </li>
      </ul>
    </div>
    <ul class="slider-pagi"></ul>
    <div class="slider">
      <div class="slide slide-0 active">
        <div id="scene">
          <div data-depth="0.00" class="layer"></div>
          <div data-depth="0.88" class="stars-cluster stars-cluster-1 layer">
            <img src="../../static/img/banner_icon03.png" class="banner-icon03">
          </div>
          <div data-depth="0.99" class="moon-wrapper layer">
            <img src="../../static/img/banner_icon02.png" class="banner-icon02">
          </div>
          <div data-depth="0.66" class="title-wrapper layer">
            <img src="../../static/img/banner_icon01.png" class="banner-icon01">
          </div>
          <div data-depth="0.77" class="title-wrapper layer">
            <div class="btn" v-if="this.$route.name === 'Index'">
              <router-link to="/createLove"  class="btn-left">发布誓言</router-link>
              <router-link to="/myLove"  class="btn-right">我的誓言</router-link>
            </div>
            <div class="btn" v-else-if="this.$route.name === 'CreateLove'">
              <router-link to="/index"  class="btn-left">爱情誓言</router-link>
              <router-link to="/myLove"  class="btn-right">我的誓言</router-link>
            </div>
            <div class="btn" v-else-if="this.$route.name === 'MyLove'">
              <router-link to="/index"  class="btn-left">爱情誓言</router-link>
              <router-link to="/createLove"  class="btn-right">发布誓言</router-link>
            </div>
          </div>
        </div>
      </div>
      <div class="slide slide-1">
        <div id="scene2">
          <div data-depth="0.00" class="layer"></div>
          <div data-depth="0.88" class="stars-cluster stars-cluster-1 layer">
            <img src="../../static/img/banner_icon03.png" class="banner-icon03">
          </div>
          <div data-depth="0.66" class="title-wrapper layer">
            <img src="../../static/img/banner_icon04.png" class="banner-icon04">
            <img src="../../static/img/banner_icon05.png" class="banner-icon04 banner-icon05">
            <img src="../../static/img/banner_icon06.png" class="banner-icon04 banner-icon06">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'Banner'
  }
</script>



